import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    isLogin: 0
  },
  mutations: {
    loginOut (state) {
    	state.isLogin=0;
    },
    login(state){
    	state.isLogin=1;
    }
  }
})
export default store


